/*HEADER*/

.btn-show {
  display: block;
}

.header {
  &-main-wrap {
    display: flex;
    justify-content: center;
    box-shadow: 0 0 16px rgba(38, 38, 38, 0.16);
    height: 72px;
  }

  &-main {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 956px;
    //width: 100%;
    //padding: 0 81px;

    &__logo {
      width: auto;
    }
  }
}

.delivery-section {
  display: flex;
  align-items: center;
}

.button-time-delivery {
  @extend %general--btn;
  display: block;
  width: 65px;
  min-width: 57px;
  font-size: $font_size-xl;
  line-height: $line_height-xl;

  &__hint {
    display: none;
    font-size: 11px;
    line-height: 13px;
    color: #626262;
  }

  &:hover {
    cursor: pointer;
  }
}

.input-location-hint {
  display: block;
  text-align: center;
  width: 32px;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  color: #626262;
}

.input-location {
  display: flex;
  align-items: center;
  position: relative;

  &__input {
    @extend %general--btn;
    width: 292px;
    border: 1px solid #e0e0e0;
    box-sizing: border-box;
    border-radius: 2px;
    font-size: $font_size-xl;
    line-height: $line_height-xl;
    padding-left: 36px;
  }

  &__input::placeholder {
    color: black;
    font-size: $font_size-xl;
    line-height: $line_height-xl;
  }

  &__link {
    position: absolute;
    margin-left: 12px;
    margin-top: 2px;
  }
}

.login-section {
  display: flex;
  align-items: center;

  .button-sign-in {
    @extend %general--btn;
    width: 80px;
    min-width: 72px;
    //margin-left: 92px;
    font-weight: 500px;
    font-size: $font_size-m;
    line-height: $line_height-xl;
    &:hover {
      background-color: #262626;
      color: #fff;
      cursor: pointer;
    }
  }

  .button-register {
    @extend %general--btn;
    width: 80px;
    min-width: 72px;
    margin-left: 16px;
    font-weight: 500px;
    font-size: $font_size-m;
    line-height: $line_height-xl;

    &:hover {
      background-color: #262626;
      color: #fff;
      cursor: pointer;
    }
  }

  .icon-basket {
    margin-left: 27px;
  }
}

//.header-search-wrap {
//  display: flex;
//  justify-content: center;
//}

.header-search {
  width: 956px;
  height: 79px;
  border-bottom: 1px solid #e0e0e0;

  &-wrap {
    display: flex;
    justify-content: center;
  }

  &__box {
    display: flex;
    align-items: flex-end;
    position: relative;
    padding-top: 40px;
  }

  &__input {
    width: 100%;
    border: 0 solid transparent;
    font-size: 16px;
    line-height: 24px;
    margin-left: 8px;
    color: #626262;
  }
}

.header-hidden {
  display: none;
  //display: grid;
  grid-template-columns: repeat(2, auto);
}
